@import url(https://use.fontawesome.com/releases/v5.5.0/css/all.css);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.hidden-appearance {
  opacity: 0; }

div.navbar-item {
  cursor: pointer; }

.avatar-img {
  border-radius: 50%; }

.hero-uziday-page {
  height: calc(100vh - 70px) !important;
  min-height: calc(100vh - 70px) !important;
  max-height: calc(100vh - 70px) !important; }

body, html {
  font-family: 'Montserrat', sans-serif;
  scroll-behavior: smooth !important; }

.animation-delay-100 {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s; }

.animation-delay-200 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s; }

.is-hidden-uziday-version {
  opacity: 0; }

.is-uziday-primary {
  background-color: #F45E3E !important;
  color: #fff !important; }


@media screen and (max-width: 1000px) {
  .navbar {
    padding-left: 0px !important; }
    .navbar .navbar-brand .navbar-burger {
      margin-top: 10px;
      margin-right: 5px;
      border-radius: 5px;
      transition: 0.3s;
      height: 50px; }
      .navbar .navbar-brand .navbar-burger:hover {
        background-color: #fafafa;
        transition: 0.3s; }
    .navbar .navbar-brand div.navbar-item {
      padding-top: 15px; }
    .navbar .navbar-menu {
      padding-right: 0px !important;
      text-align: center; }
      .navbar .navbar-menu .navbar-item {
        padding: 10px 10px 10px 10px !important; }
        .navbar .navbar-menu .navbar-item:hover {
          background-color: #fafafa !important; } }

.navbar {
  background-color: white !important;
  padding-left: 14px;
  height: 70px;
  box-shadow: 0px 0px 1px #fff !important; }
  .navbar .navbar-brand div.navbar-item {
    cursor: pointer;
    transition: all 0.3s; }
    .navbar .navbar-brand div.navbar-item a {
      transition: 0.3s; }
    .navbar .navbar-brand div.navbar-item:hover {
      transition: 0.3s;
      -webkit-transform: translateY(-3px);
              transform: translateY(-3px); }
      .navbar .navbar-brand div.navbar-item:hover a {
        transition: all 0.3s;
        color: #F45E3E; }
  .navbar .navbar-menu {
    padding-right: 15px; }
    .navbar .navbar-menu .navbar-item {
      transition: 0.3s;
      padding: 5px 30px 0px 30px;
      font-size: 18px; }
      .navbar .navbar-menu .navbar-item:hover {
        transition: 0.3s;
        background-color: #fff;
        color: #F45E3E; }
        .navbar .navbar-menu .navbar-item:hover .navbar-link {
          background-color: #fff;
          color: #F45E3E; }

.switch-uziday .button {
  border: none;
  width: 50% !important; }

.landing-hero.hero {
  background-color: #fff; }
  .landing-hero.hero .hero-body .nube {
    position: absolute;
    z-index: 2;
    width: auto !important;
    transition: 0.3s; }
    .landing-hero.hero .hero-body .nube img {
      transition: 0.3s;
      opacity: .7 !important; }
      .landing-hero.hero .hero-body .nube img:hover {
        transition: 0.3s;
        opacity: 1 !important; }
  .landing-hero.hero .hero-body .nube.nube1 {
    top: 80px;
    left: 80px; }
  .landing-hero.hero .hero-body .nube.nube2 {
    left: 50px; }
  .landing-hero.hero .hero-body .nube.nube3 {
    top: 80px;
    right: 80px; }
  .landing-hero.hero .hero-body .nube.nube4 {
    right: 120px; }
  .landing-hero.hero .hero-body .container {
    z-index: 3 !important; }
    .landing-hero.hero .hero-body .container img {
      margin-top: -40px; }
    .landing-hero.hero .hero-body .container .title {
      transition: 0.3s;
      padding-bottom: 20px; }
    .landing-hero.hero .hero-body .container .brandLogo {
      width: 200px; }
    .landing-hero.hero .hero-body .container .i-want-subscribe {
      background-color: #268E93;
      color: #fff;
      border-radius: 5px !important;
      border: 0px !important; }

.animation {
  transition: 0.3s !important;
  -webkit-transform: scale(0.9);
          transform: scale(0.9); }

.animation.zoomIt {
  transition: 0.3s !important;
  -webkit-transform: scale(1) !important;
          transform: scale(1) !important; }

.navbar-shape{
	width: 100%;
	height: 70px;
	background-color: transparent !important;
}
.is-uzi-emoticon {
  width: 70px; }

.app-hero .columns .box {
  transition: 0.3s;
  box-shadow: 0px 0px 1px gainsboro;
  cursor: pointer; }
  .app-hero .columns .box:hover {
    transition: 0.3s;
    -webkit-transform: translateY(-10px) !important;
            transform: translateY(-10px) !important; }

.team .founder {
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0px 0px 1px grey; }
  .team .founder .name {
    font-size: 24px; }
  .team .founder .description {
    font-size: 18px; }

.redes {
  display: none; }
  .redes .bg-close {
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(51, 51, 51, 0.2);
    width: 100%;
    height: 100%;
    z-index: 100; }
  .redes .uzi-modal {
    width: 80%;
    left: 10%;
    box-shadow: 1px 1px 3px grey !important;
    position: fixed;
    height: 95vh;
    z-index: 101;
    padding: 0;
    bottom: 0 !important;
    background-color: white;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px; }
    .redes .uzi-modal .uzi-modal-content {
      width: 100%;
