.redes.is-active {
  display: block; }

@media screen and (max-width: 760px) {
  .uzi-modal {
    width: 98% !important;
    left: 1% !important; }
    .uzi-modal .uzi-modal-head .uzi-modal-title {
      text-align: left; } }

